<template>
    <div>
        <p>* Pastikan data yang kamu masukkan belum ada <br>
          ** Terima kasih kontribusinya! data akan direview sebelum tampil</p>

        <label for="name">Nama</label>
        <input type="text" v-model='name'> <br>

        <label for="city">Kota</label>
        <input type="text" v-model='city' placeholder="kalau umum tulis umum"> <br>

        <label for="link">link</label>
        <input type="url" v-model='link'> <br>

        <label for="tag">Tag</label>
        <input type="text" v-model='tag' placeholder="kategori: css/html/design/umum dll. (satu saja)"> <br>

        <input type="submit" value="Submit Data" @click="AddData" id="submitBtn">
        <br>

    </div>
</template>

<script>

export default {
  name: 'FormInput',
  data() {
    return {
        name: '',
        city: '',
        link: '',
        tag: '',
    }
  },
  methods: {
      AddData (e) {
         var datas = {
              'name': this.name,
              'city': this.city,
              'link': this.link,
              'tag': this.tag,
          }
          this.$store.dispatch('addItem', datas)
         //empty form
          this.name = ''
          this.city = ''
          this.link = ''
          this.tag = ''
      }
  }
}
</script>

<style scoped>
label, input{
    display: block;
    width: 100%;
}

#submitBtn{
    border: 0px;
    background-color: #42b983;
    color: white;
}
</style>
