<template>
  <div class="hello">

       <div class="is-center">
          <input type="text" v-model="searchText" placeholder="cari kota/kategori">
      </div>

     <div class="is-center">
          <h2> {{ searchText}} </h2>

          <div v-for="item in items">
              <p> <a v-bind:href="item.fields.link.stringValue"  target="_blank">
                   {{ item.fields.name.stringValue }} </a>
               - {{ item.fields.city.stringValue }} - {{ item.fields.tag.stringValue }}
              </p>
          </div>
      </div>

    </div>
</template>

<script>

export default {
  name: 'Items',
  data(){
      return {
          searchText: ''
      }
  },
  created(){
    this.$store.dispatch('loadItems')
  },
  computed: {
      items() {
          if(this.searchText == '')
              return this.$store.getters.approvedItems
         else
             return this.$store.getters.filteredItems(this.searchText)
      }
  }
}
</script>

