import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from './router'

Vue.use(Vuex)

export default new Vuex.Store({
   state: {
    items: []
  },
  getters: {
      approvedItems: (state, getters) => {
          return state.items.filter(function(object, key){
              return object.fields.status.booleanValue == true
          })
      },
      filteredItems: (state, getters) => (keyword) => {
          return state.items.filter(function(object, key){
              return object.fields.status.booleanValue == true &&
                    (object.fields.name.stringValue.toLowerCase().indexOf(keyword) >= 0
                     || object.fields.city.stringValue.toLowerCase().indexOf(keyword) >= 0
                    )
                })
      },
      unapprovedItems: (state, getters) => {
          return state.items.filter(function(object, key){
              return object.fields.status.booleanValue == false
          })
      },
  },
  mutations: {
      SET_ITEMS(state, items) {
        state.items = items.data.documents
      },
      ADD_ITEM(state, items) {
          router.push("/")
      }
  },
  actions: {
    loadItems ({ commit, state }) {
        axios.get('https://firestore.googleapis.com/v1beta1/projects/community-list/databases/(default)/documents/communities')
                .then(items => {
                    commit('SET_ITEMS', items)
                })
    },
    addItem({commit, state}, item) {
        if ( !item.name && !item.city && !item.link && !item.tag ) {
            console.log('empty field not allowed!')
        } else {

            //verify: check if alread in the doc

            const formatData = {
                "name": { "stringValue": item.name },
                "city": { "stringValue": item.city },
                "link": { "stringValue": item.link },
                "tag": { "stringValue": item.tag },
                "status": { "booleanValue": false }
            }

            axios.post("https://firestore.googleapis.com/v1beta1/projects/community-list/databases/(default)/documents/communities",
                        {"fields": formatData}, {headers: { "Content-Type": "application/json" }})
                .then(_ => {
                    commit('ADD_ITEM', item)
                })
        }
    }

  }
})
