<template>
  <div class="home">
      <div v-for="item in items">
          <p>{{ item.name }} </p>
          <span>{{ item.fields.name }}</span>
          <span>{{ item.fields.link }}</span>
          <span>{{ item.fields.city }}</span>
          <span>{{ item.fields.tag }}</span>
          <hr>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Items',
  created(){
    this.$store.dispatch('loadItems')
  },
  computed: {
      items() {
          return this.$store.getters.unapprovedItems
      }
  }
}
</script>
